.course-page {
    padding: 20px;
  }
  
  .search-bar {
    margin-bottom: 20px;
    padding: 10px;
    width: 100%;
    max-width: 300px;
  }
  
  select {
    margin-bottom: 20px;
    padding: 5px;
    width: 200px;
  }
  
  .course-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
  }
  
  .course-card {
    padding: 15px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .enroll-btn {
    background-color: #4CAF50;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .enroll-btn:hover {
    background-color: #45a049;
  }
  
  .course-details {
    margin-top: 20px;
  }
  
  .course-details ul {
    list-style-type: none;
    padding: 0;
  }
  