.home-container {
    text-align: center;
    padding: 50px;
    background-color: #f4f4f9;
  }
  
  .home-header {
    margin-bottom: 30px;
  }
  
  .home-header h1 {
    font-size: 3rem;
    color: #333;
  }
  
  .home-header p {
    font-size: 1.2rem;
    color: #666;
  }
  
  .cta-buttons {
    margin-top: 20px;
  }
  
  .cta-button {
    padding: 10px 20px;
    font-size: 1rem;
    margin: 0 10px;
    border: none;
    border-radius: 5px;
    background-color: #007BFF;
    color: white;
    cursor: pointer;
  }
  
  .cta-button:hover {
    background-color: #0056b3;
  }
  
  .features-section {
    background-color: #fff;
    padding: 30px;
    margin-top: 50px;
    border-radius: 8px;
  }
  
  .features-section h2 {
    color: #333;
  }
  
  .features-section ul {
    list-style-type: none;
    padding: 0;
  }
  
  .features-section li {
    font-size: 1.1rem;
    color: #444;
  }
  
  .image-section {
    margin-top: 30px;
  }
  
  .image-section img {
    width: 100%;
    max-width: 600px;
    height: auto;
    border-radius: 8px;
  }
  