.course-card {
    padding: 20px;
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 10px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .course-card h2 {
    color: #333;
  }
  
  .course-card p {
    font-size: 16px;
    color: #555;
  }
  
  .enroll-btn {
    padding: 10px 15px;
    background-color: #ff5733;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .enroll-btn:hover {
    background-color: #e04f2b;
  }
  
  .course-details {
    margin-top: 15px;
  }
  